.App-header {
    /*width: 100vw;*/
    font-size: 12.5vmin;
    color: #495057;
}

.App-footer {
    display: flex;
    min-height: 7%;
    width: 100%;
    justify-content: flex-end;
    align-items: center;
    color: #282c34;
    background-color: #e2d79c;
    position: fixed;
    bottom: 0;
    display: flex;
    justify-content: space-between;
}

.App-link {
    color: #61dafb;
}

button, a {
    font-size: 3vmin;
    -webkit-appearance: none;
    border: 0;
    background: transparent;
    text-decoration: none;
}

html, body {
    margin: 0;
    padding: 0;
    overflow: hidden
}

svg {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
}

.jumbo {
    color: inherit !important;
    font-size: inherit !important;
}

@-webkit-keyframes grow {
    from {
        transform: scale(1);
    }
    to {
        transform: scale(4.333);
    }
}

@keyframes grow {
    from {
        transform: scale(1);
    }
    to {
        transform: scale(4.333);
    }
}

